import React from 'react';
import styles from './styles.module.css';
import { Link } from 'react-router-dom';

const CBNlist = () => {

    return(
        <div className={styles.main}>
            <div className={styles.grid}>
                <div className={styles.slides}>
                    <div className={styles.img}>
                    <h1>NMFB Loan Calculator</h1>
                    <h2>CBN Intervention</h2>
                    <Link to ="/Covid19" className={`${styles.btn} ${styles.light}`}>COVID19 TCF</Link>
                    <Link to ="/ABP" className={`${styles.btn} ${styles.light}`}>ABP</Link>
                    <Link to ="/AGSMEIS" className={`${styles.btn} ${styles.light}`}>AGSMEIS</Link>
                    <Link to ="/NYIF" className={`${styles.btn} ${styles.light}`}>NYIF</Link>
                    </div>
                </div>
                <div className={styles.slides2}>
                   
              
             
                </div>
            </div>
        </div>
    )
}
export default CBNlist;