import React from 'react';
import styles from './styles.module.css';
import { Link } from 'react-router-dom';


const Slider = () => {
    return(
        <>
       <div className={styles.main}>
            <div className={styles.grid}>
                <div className={styles.slides}>
                    <div className={styles.img}>
                    <h1>NMFB Loan Calculator</h1>  
                    <h2>Are you an existing NMFB Loan Beneficiary?</h2>  
                    <div className={styles.grids}>
                <Link to="/Benficiary" className={`${styles.btn} ${styles.light}`}>Yes</Link>
                <Link to="/NonBenficiary" className={`${styles.btn} ${styles.dark}`}>No</Link>
                    </div>
                    </div>
                </div>
                <div className={styles.slides2}>
              
                </div>
            </div>
        </div> 
        </>
    )
}
export default Slider;